      </div>
      <div class="r">
        <div class="row">
          <span>姓名:</span>
          <span>{{userInfo.name}}</span>
        </div>
        <div class="row">
          <span>电话:</span>
          <span>{{userInfo.mobile || '暂无'}}</span>
        </div>
        <div class="row">
          <span>工号:</span>
          <span>{{userInfo.jobnumber || '暂无'}}</span>
        </div>
      </div>
