<script>
export default {
  name: 'Admin',
  data () {
    return {
      userInfo: {
        name: '',
        mobile: '',
        jobnumber: '',
        avatar: ''
      }
    }
  },
  methods: {
    getUserInfo () {
      this.$rpc.call('cdtct_dingtalk.cdtct_dingtalk_users', 'get_index_user', null, {}).then(res => {
        this.userInfo = res
        sessionStorage.setItem('userInfo', JSON.stringify(this.userInfo))
      }).catch(e => {
        console.log(e)
      })
    },
    scan () {
      let that = this
      // eslint-disable-next-line
      dd.biz.util.scan({
        type: 'qrCode',
        tips: '打卡',
        onSuccess (data) {
          let text = data.text
          // eslint-disable-next-line
          text = text.replace(/\'/g, '"')
          let qrcodeData = JSON.parse(text)
          that.$rpc.call(qrcodeData.model, qrcodeData.func, null, Object.assign({}, qrcodeData, { user_id: that.userInfo.id })).then(res => {
            alert(res)
          }).catch(e => {
            console.log('e', e)
          })
        },
        onFail (err) {
          console.log(err)
          alert('shibai')
        }
      })
    }
  },
  mounted () {
    this.getUserInfo()
  }
}
</script>

